@use 'variables' as vars;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-family: vars.$main_font;
    font-size: 1.6rem;

    &.fix-body{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 8.6rem;
    }
}

p{
    font-size: 2rem;
    color: vars.$black;
    line-height: 1.5;
}

.container{
    width: min(95%, 120rem);
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2 , h3{
    margin: 0 0 3rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}